
export default {
	onLaunch: function() {
		console.log('App Launch')
		let uid=''
		 uid=uni.getStorageSync('uid');
		 console.log(uid)
		if(uid==''){
			uni.showToast({
				icon:'none',
				title: '您还没有登录，请登录'
			});
			uni.navigateTo({
				url: '/pages/login/login?type=1'
			});
		}
	},
	onShow: function() {
		console.log('App Show')
	},
	onHide: function() {
		console.log('App Hide')
	}
}
